import React, { useState } from 'react'
import '../style/footer.css'
// <Langs swapLanguage={ swapLanguage }/> 
const Footer = () => {

    const [email, setState] = useState(
        <button className="contact-btn" onClick={() => {
            setState(
                <a className="contact-link" href="mailto:miikatoivanen91@gmail.com">
                    miikatoivanen91@gmail.com
                </a>
                )}
            }>
            Ota Yhteyttä
        </button>
    )

    return(
        <footer id="p-footer">
            <div className="f-container">
                <p>
                    Mikäli on kysyttävää, tarvetta nettisivuille, tai toivotte minua mukaan johonkin projektiin
                </p>
                {email}
            </div>
        </footer>
    )

}

export default Footer;