import Header from './Header'
import Content from './Content'
import Footer from './Footer'
import Intro from './Intro'
import React, { useState } from 'react'

// app styling is in index.css
// https://react-icons.github.io/react-icons

function App() {

  const [language, swapLanguage] = useState('suomi')
  const [menuopen, menuAction] = useState("closed")

  const openMenu = () => {
    if (menuopen === "closed")
      menuAction("open")
    else
      menuAction("closed")
  }

  const smoothScroll = (elemid) => {
    let wsHeight = document.getElementById(elemid).offsetTop
    window.scrollTo({ top: wsHeight, behavior: 'smooth' })
  }

  const navmenu = [
    ["etusivu", "minusta", "projektit", "ota yhteyttä"],
    ["p-header", "p-intro", "p-projects", "p-footer"] //element ids
  ]

  const setNavigationMenuByLanguage = () => {
    let menu = navmenu[0].map((item, index) => {
      return (
        <button key={"nav-btn-" + index} onClick={() => { smoothScroll(navmenu[1][index]) }} className="hm-list-item">
          <span >{item}</span>
        </button>
      )
    })
    return menu;
  }

  return (
    <div id="page-columns">

      <div id="menu-column" className={menuopen}>
        <div id="header-menu" className={menuopen}>
          <div className="header-menu-list">
            {setNavigationMenuByLanguage()}
          </div>
          <button className="openingBtn" onClick={openMenu}>
            <div className={"hamburger "+menuopen}>
              <div className="top"></div>
              <div className="middle">
                <div className="arrowLeft"></div>
              </div>
              <div className="bottom"></div>
            </div>
          </button>
        </div>
      </div>

      <div id="body-column">
        <Header  />
        <Intro />
        <Content />
        <Footer />
      </div>

    </div>
  )
}

export default App