import { FaPython, FaUnity, FaWordpress, FaCss3Alt, FaReact, FaPhp } from 'react-icons/fa'
import { SiBlender, SiHtml5, SiNodeDotJs, SiMysql, SiMongodb } from 'react-icons/si'
import '../style/container.css'

const Container = () => {
    const iconsize = 80;
    return(
        <div className="box-container">
            <div className="box">
                <span><SiHtml5  size={iconsize}/> <FaCss3Alt  size={iconsize}/></span>
                <span>HTML / CSS</span>
            </div>
            <div className="box">
                <span> <SiNodeDotJs  size={iconsize}/></span>
                <span>JavaScript / Node Js</span> 
            </div>
            <div className="box">
                <span><FaPython  size={iconsize}/> </span>
                <span>Python</span>
            </div>
            <div className="box">
                <span><FaUnity  size={iconsize}/></span>
                <span>Unity</span>
            </div>
            <div className="box">
                <span><SiBlender  size={iconsize}/></span>
                <span>Blender</span>
            </div>
            <div className="box">
                <span><FaWordpress  size={iconsize}/></span>
                <span>Wordpress</span>
            </div>
            <div className="box">
                <span><FaReact  size={iconsize}/></span>
                <span>React / *Native</span>
            </div>
            <div className="box">
                <span><FaPhp  size={140}/></span> 
            </div>
            <div className="box">
                <span><SiMysql  size={120}/></span> 
            </div>
            <div className="box">
                <span><SiMongodb size={iconsize}/></span>
                <span>MongoDb</span>
            </div>
        </div>
    )
}

export default Container;