import AboutMeFin from './AboutMe-fin'

import '../style/intro.css'

const Intro = () => {

    const getMyAge = () => {
        let birthDate = new Date(1991, 5, 2)
        let timeDiff = Date.now() - birthDate.getTime()
        let myAge = new Date(timeDiff)
        return Math.abs(myAge.getFullYear() - 1970)
    }

    const getCodingTime = () => {
        let dateOfStart = new Date(2019,1,1)
        let timeDiff = Date.now() - dateOfStart.getTime()
        let codingTime = new Date(timeDiff)
        return Math.abs(codingTime.getFullYear() - 1970)
    }

    return(
        <div className="intro" id="p-intro">
            <AboutMeFin getMyAge={ getMyAge } getCodingTime={getCodingTime}/>
        </div>
    )

}

export default Intro;