
const AboutMeFin = ({ getMyAge, getCodingTime }) => {
    return(
        <>
        <div className="text_container">
            <h1>Aluksi...</h1>
            <p>
                Olen { getMyAge() }-vuotias itseopiskellut koodari. Hommani lähti liikkeelle noin { getCodingTime() }-vuotta sitten kun eräs ystäväni heitti ilmoille virkkeen, 
                jota tänäkin päivänä on saanut kuulla paljon: "koodareista on pulaa." Niimpä otin asiakseni tutustua aiheeseen ja heti kotiin päästyänyäni etsin YouTubesta koodaamiseen johdattelevia videoita. 
                Muutaman ensimmäisen videon jälkeen kirjoitin ensimmäisen HTML sivuni. Heti kun omat tuotokseni tulivat selaimessa näkyviin, itsessäni syttyi kipinä koodailuun joka on kestänyt aina tähän päivään asti.
            </p>
        </div>
        
        <div className="text_container">
            <h1>Kehittyminen</h1>
            <p>
                Alotin kirjoittamalla staattisia HTML sivuja, joihin pian aloin lisäämään tyyliä CSS:llä ja dynaamisuutta javascriptillä. Pian tutustuin myös PHP-kieleen, sekä MySQL-tietokantoihin.
                Näiden parissa ollessani huomioni kiinnittyi Wordpress -nimiseen alustaan, jolle aloin pian tekemään nettisivuja itsekin.
            </p>
            <p>
                WordPressin ihmeellisessä maailmassa jonkin aikaa ja sinä aikana alotin yritystoiminnan, jonka tarkotuksena on tehdä wordpress pohjaisia nettivuja eri asiakaskunnille.
            </p>
            <p>
                Jonkin ajan kuluttua tästä aloin tutustumaan syvemmin Node Js:n tuomiin mahdollisuuksiin. Aiemmin olin ainoastaan tutustunut selaimessa toimivaan Javascriptiin, 
                mutta hiljalleen aloin ymmärtämään miten sitä voi käyttää paljon monipuolisemmin myös palvelimena Node Js:n kautta. 
                Pidin siitä, että yhdellä ohjelmointikielellä voin hallita koko pakettia selaimessa niin kuin myös palvelimessa.
            </p>
            <p>
                Siitä asti olen viettäynyt eniten aikaa Javascript kielessä, jonka käytössä koen olevani vahvimmillani. React -nimiseen "frameworkiin" olen tutustunut kaikista viimeisimmin ja kun on taas aika oppia uutta, aion tutustua muihin frameworkeihin kuten Angular ja Vue.
            </p>
            <p>
                Välillä kun on extra aivokapasiteettia, niin koitan syventää ymmärttystäni myös Java, Python ja C# -kielissä. 
                Näillä tosin en ole ehtinyt tekemään mitään kunnollista, ainoastaan muutamia irrallisia kokeiluja. Samoin olen yrittynyt tutustua peli- ja 3D teknologiaan Unityn ja Blender sovellusten kautta. 
                Olen myös kiinnostunut siitä miten selaimeen voi tuoda 3D mallinnuksia Blender-sovelluksesta, esimerkiksi THREE JS -nimisen JavaScript-kirjaston kautta.
            </p>
        </div>
        <div className="text_container">
            <h1>Kiinnostuksen kohteet</h1>
            <p>
                Oma intohimoni on tehdä nettisivuja. Tähtäimessä on siis olla nettisivujen tekijä joko yrityksen palveluksessa, tai itsenäisenä yrittäjänä.
            </p>
            <p>
                Myös muunlaiset projektit kiinostavat, kuten peli- tai muun virtuaalimaailman luominen.
                Siksi olen myös valmis tarttumaan haasteeseen jos ikinä pääsee sellaiseen projektiin mukaan. 
            </p>
        </div>
        </>
    )
}

export default AboutMeFin;