import React, { useState } from 'react'
import { AiOutlineRight } from 'react-icons/ai'
import '../style/projects.css'

//AiOutlineRight

const Projects = () => {

    const [links, changeLink] = useState({
        activeLink: null,
        lists: [
            {
                txt: "HTML / CSS / JavaScript",
                items: [
                    {txt:'näppäily harjoituksia', href: "./keyboardtyping"},
                    {txt:'Astar pathfinder', href: "./pathfind"},
                    {txt:'lights behaviour', href: "./light"},
                    {txt:'platform movement and collisions', href: "./platform2D"},
                    {txt:'reittivalinta', href: "./routes"},
                    {txt:'"Komeetta" -peli', href: "./comet"},
                ]
            },
            {
                txt: "Blender",
                items: [
                    {txt:'low poly models', href: "./modelviewer"},
                ]
            }
        ]
    })
    const collapseList = index => {

        if(linkActive(index) === "menu-button active") {
            changeLink({...links, activeLink: null})
            return false
        }
        changeLink({...links, activeLink: links.lists[index]})
        return true
    }
    const linkActive = index => {
        return links.lists[index] === links.activeLink ? 'menu-button active' : 'menu-button inactive'
    }
    const collapsed = index => {
        return links.lists[index] === links.activeLink ? 'project-display' : 'project-display collapsed'
    }
    const arrowSize = 20

    return(

        <div className="projects-box" id="p-projects">
        <span className="subheader">Lyhyitä projekteja ja harjoitustöitä</span>
            <div className="project-menu">

                { links.lists.map( (e, index) => (
                    <div className="menuholder" key={"div-"+index}>
                        <button className={linkActive(index)} onClick={() => { 
                                let isCollapsed = collapseList(index)
                                for(let i = 0; i < links.lists.length; i++) {
                                    if( i === index && isCollapsed) {
                                        document.getElementById('menu-'+i).style.height = (e.items.length * 34 +20) +"px"
                                    }
                                    else {
                                        document.getElementById('menu-'+i).style.height = 0+"px";
                                    }
                                }
                            }}>
                            {links.lists[index].txt}
                            <AiOutlineRight 
                                size={ arrowSize }
                                className="arrow-right" />
                        </button>

                        <div key={"menu-"+index} id={"menu-"+index} className={collapsed(index)}>
                            {links.lists[index].items.map((item, i) => (
                                <a key={"a-"+index+"-"+i} className="link-item" href={item.href}>{item.txt}</a>
                            ))}
                        </div>
                    </div>

                ))}

            </div>

        </div>
    )
}

export default Projects